      this.tag1 = false
    },
    loadMore () {
      alert(`loadMore`)
    },
    searchAction (e) {
      this.value = e
      alert(this.value)
    },
    searchCancel (e) {
      this.value = ''
    },
    inputAction (e) {
      this.inputValue = e
    },
    modalClick1 (e) {
      this.modalVisible1 = true
      this.zIndex++
    },
    modalClick2 (e) {
      this.modalVisible2 = true
      this.zIndex++
    },
    modalClick3 (e) {
      this.modalVisible3 = true
      this.zIndex++
    },
    modalClose1 (e) {
      this.modalVisible1 = false
    },
    modalClose2 (e) {
      this.modalVisible2 = false
    },
    modalClose3 (e) {
      this.modalVisible3 = false
    },
