        content: '3',
        autoClose: true,
        maskClosable: false
      })
    },
    tableAdd(row, index) {
      this.zIndex++
      this.popupVisible1 = false
      this.$modal.alert({
        title: '',
        content: '3',
        autoClose: true,
        maskClosable: false
      })
    },
    tableEdit(row, index) {
      this.zIndex++
      this.popupVisible1 = false
      this.$modal.alert({
        title: '' + index + '',
        content: row,
        autoClose: true,
        maskClosable: false
      })
    },
    tableDel(row, index) {
      this.zIndex++
      this.popupVisible1 = false
      this.$modal.alert({
        title: '' + index + '',
        content: row,
        autoClose: true,
        maskClosable: false
      })
    },
    loadedAction () {
      this.$toast.text({content: '', direction: 'bottom'})
    },
    // 
    openCurKeyboard (e) {
      const obj = JSON.parse(e.currentTarget.dataset.tips)
      this.curVal = e.currentTarget.dataset.val
      this.keyBoardVisible = true
      this.tips = {title: obj.title, placeholder: obj.placeholder, name: obj.name, unit: obj.unit, isCard: obj.isCard}
    },
    // 
    save (data) {
      this.keyBoardVisible = false
      if (data.val && data.val !== '') {
        this[[data.name]] = data.val + data.unit
      } else {
        this[[data.name]] = ''
      }
    },
    keyBoardClose () {
      this.keyBoardVisible = false
    },
    mockList (size) {
      return new Promise(resolve => {
        const list = []
        for (let index = 0; index < size; index++) {
          list.push({
            name: `${index}`,
            sex: '0',
            num: 123,
            age: 18,
            num2: 234,
            rate: 3,
            address: 'shenzhen'
          })
        }
        resolve(list)
      })
    }
  },
  created() {
    this.mockList(500).then(data => {
      this.tableData = data
    })
  },
  destroyed () {
    document.removeEventListener("scroll", this.handleScroll);
  },
  mounted () {
    window.addEventListener("scroll", this.handleScroll);
    this.changeLimit();
  }
}
</script>

