}
.selectSign .dm__cell--left .img{
    width: 50px;
}
.selectSign .dm__cell--right{
    flex: 4!important;
    justify-content: flex-start!important;
    text-align: left!important;
}
.selectSign .dm__cell--right p{
    display: flex;
    align-items: center;
    margin: 1rem 0;
}
.selectSign .dm__cell--right p:last-child{
    color: #999;
    font-size: 1.4rem;
}
.selectSign .dm__cell--right p .dm__tag{
    padding:3px 4px;
    font-size: 1.1rem!important;
    margin-left: .5rem;
}
.goTopView {
  position: fixed;
  right: 10px;
  bottom: 10px;
  z-index: 999999;
  cursor: pointer;
  .dm__icon--totop {
    font-size: 3.5rem;
  }
}
.dm-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
}

.dm-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
}
  
.dm-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
}
.dmDatePickerView {
  width: 17%;
  height: 40px;
  border-radius: 4px;
  padding-left: 40px;
  margin: 10px auto;
  line-height: 40px;
  position: relative;
  min-width: 220px;
  .iconfont {
    position: absolute;
    left: 5px;
    top: 1px;
    color: #c0c4cc;
  }
  .dm-range-editor  {
    border: none;
    width: 220px;
  }
}
</style>




